<template>
  <div class="wrapper">
    <div class="map" id="olMap"></div>
  </div>
</template>

<script>
import "ol/ol.css";
import { Tile as TileLayer } from "ol/layer";
import XYZ from "ol/source/XYZ";
import { defaults as defaultControls } from "ol/control";
import Map from "ol/Map.js";
import View from "ol/View.js";
import { Feature } from "ol";
import { Polygon } from "ol/geom";
import { Vector as VectorLayer } from "ol/layer";
import { Vector as VectorSource } from "ol/source";
import { Style, Stroke, Fill } from "ol/style";

let map = null;
export default {
  data() {
    return {
      
    }
  },
  mounted() {
    this.initMap();
  },
  beforeDestroy() {
    this.clearMap();
  },
  //
  computed: {
    nodeInfoAll() {
      return this.$store.state.results
    },
    nodeInfo() {
      return this.$store.state.results.nodeInfo
    },
    nodeSpatical() {
      return this.$store.state.results.nodeSpatical
    },
  },
  methods: {
    initMap() {
      map = new Map({
        target: "olMap",
        view: new View({
          center: [114.5, 30.5], //中心点经纬度
          zoom: 5, //图层缩放大小
          projection: "EPSG:4326",
        }),
        controls: defaultControls({
          zoom: true,
          attribution: false,
          rotate: false,
        }),
      });

      // 添加地图
      let url = "http://t{0-7}.tianditu.com/DataServer?x={x}&y={y}&l={z}";
      url = `${url}&T=vec_c&tk=7a574f33b90173055cef00fca14a889a`;
      const source = new XYZ({
        url: url,
        projection: "EPSG:4326",
      });
      const tdtLayer = new TileLayer({
        source: source,
      });
      map.addLayer(tdtLayer);
      // 添加注记
      url = "http://t{0-7}.tianditu.com/DataServer?x={x}&y={y}&l={z}";
      url = `${url}&T=cva_c&tk=7a574f33b90173055cef00fca14a889a`;
      const sourceCVA = new XYZ({
        url: url,
        projection: "EPSG:4326",
      });
      const tdtcvaLayer = new TileLayer({
        source: sourceCVA,
      });
      map.addLayer(tdtcvaLayer);
      // this.addMyLayer(map);
    },
    addMyLayer(map) {
      // 创建矩形要素
      const rectangle = new Feature({
        geometry: new Polygon([
          [
            [this.nodeSpatical.southWestLatL, this.nodeSpatical.northEastLatB], // 左上角经纬度
            [this.nodeSpatical.southWestLatL, this.nodeSpatical.southWestLatB], // 左下角经纬度
            [this.nodeSpatical.northEastLatL, this.nodeSpatical.southWestLatB], // 右下角经纬度
            [this.nodeSpatical.northEastLatL, this.nodeSpatical.northEastLatB], // 右上角经纬度
            [this.nodeSpatical.southWestLatL, this.nodeSpatical.northEastLatB], // 闭合矩形
          ],
        ]),
      });
      // 设置矩形样式
      rectangle.setStyle(
        new Style({
          stroke: new Stroke({
            color: "red",
            width: 2,
          }),
          fill: new Fill({
            color: "rgba(255, 0, 0, 0.1)",
          }),
        })
      );
      // 创建矩形图层
      const rectangleLayer = new VectorLayer({
        source: new VectorSource({
          features: [rectangle],
        }),
      });
      // 添加矩形图层到地图
      map.addLayer(rectangleLayer);
    },
    clearMapLayers() {
      const layers = map.getLayers();
      console.log("当前图层",layers)
      layers.forEach((layer, index) => {
        if(index > 1){
          map.removeLayer(layer);
        }
      });
    },
    clearMap() {
      map = null
    },
  },
  watch: {
    'nodeSpatical'(newValue, oldValue) {
      // 监听到 nodeSpatical 值发生变化
      if (newValue !== oldValue) {
        this.clearMapLayers();  // 清除图层
        this.addMyLayer(map); // 重新添加图层
      }
    },
  },
};
</script>

<style scoped>
.map {
  width: 100%;
  height: 100%;
}
.wrapper {
  /* margin-top: 7.5rem; */
  height: calc(100vh - 7.5rem);
  position: relative;
}
</style>
