import myaxios from '../utils/httpRequest'

const apiList = {
   find:`/findGeoResultList`,
   tree:`/getGeoResultTree`
}

export default {
    //查找goe成果列表
    findGeoResultList(name,status){
        return myaxios({
            method:'GET',
            url:apiList.find,
            params:{
                status:status,
                resultName:name,
                includeAnnex:false,
            }
        })
    },
    //查询geo目录树
    getGeoResultTree(){
        return myaxios({
            method:'GET',
            url:apiList.tree,
        })
    }
}