<template>
  <div class="directory-container" style="opacity: 1;">
    <div class="directory-header">
      <span class="directory-title">成果目录</span>
      <span><i class="el-icon-s-fold" style="font-size:19px;color:#2E6DFC" @click="isCollapsed =!isCollapsed"></i></span>
      <!-- <el-button type="text" :icon="isCollapsed ? 'el-icon-arrow-down' : 'el-icon-arrow-up'" @click="isCollapsed =!isCollapsed"></el-button> -->
    </div>
    
    <div style="margin-top: 0px;background-color: #FFFFFF;">
      <div style="padding:10px" v-show="isCollapsed">
        <el-input
            placeholder="级别/名称等关键词"
            v-model="filterText"
            suffix-icon="el-icon-search">
        </el-input>
      </div>
      <el-collapse-transition>
        <div v-show="isCollapsed">
          <div class="transition-box">
            <el-tree class="filter-tree" :filter-node-method="filterNode" :data="data" :default-expand-all="true" :props="defaultProps" @node-click="handleNodeClick" ref="tree">
              <span slot-scope="{ node, data }" class="slot-t-node">
                <template>
                  <i
                    :class="{
                      'el-icon-folder': !node.expanded,       // 节点收缩时的图标
                      'el-icon-folder-opened': node.expanded, // 节点展开时的图标
                      'el-icon-s-claim': node.label !== '全国级' && node.label !== '区域级'  && node.label !== '项目级' && node.label !== '工点级'// data.type是后端配合提供的识别字段，最后一级
                    }"
                    style="color: #409eff;" 
                  />
                  <span>{{ node.label}}</span>
                </template>
              </span>
            </el-tree>
          </div>
        </div>
      </el-collapse-transition>
    </div>
    <div class="directory-footer" v-show="isCollapsed">
      <a class="link" @click="toTypeTable()">列表查看</a>
    </div>
    <!-- <div style="width:400px;height:100px;background-color:skyblue">
      {{$store.state.results.geoResults}}
    </div> -->
  </div>
</template>

<script>
import GeoResultMana from '@/api/GeoResultMana';

export default {
  data() {
    return {
      filterText:'', //目录树输入框
      isCollapsed: true, // 控制折叠面板的展开与收缩
      data: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
    };
  },
  watch: {
    'filterText'(val) {
      this.$refs.tree.filter(val);
    }
  },
  mounted(){
    this.getAllGeoResults();
    this.getTree();
  },
  methods: {
    //
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    //获取目录树节点坐标
    handleNodeClick(data) {
      if(data.value){
        if(data.value.resultDetails.spatical){
        this.$store.commit('results/setNodeSpatical',data.value.resultDetails.spatical);
        this.$store.commit('results/setNodeInfo',data.value);

      }else{
        this.$message({
          type:'info',
          message:'无坐标点位',
        })
      }
      }
    },
    //跳列表
    toTypeTable(){
      this.$router.push('/typeTable')
    },
    async getAllGeoResults(){
      let res = await GeoResultMana.findGeoResultList();
      console.log('成果列表',res.data.data);
      this.$store.commit('results/setGeoResults',res.data.data);
    },
    async getTree(){
      let res = await GeoResultMana.getGeoResultTree();
      this.data = res.data.data.children;
      console.log("目录树",this.data);
      // this.$store.commit('results/setGeoTreeInfos',res.data.data.children);
    },
  },
};
</script>

<style scoped>
.directory-container {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.directory-header {
  display: flex;
  align-items: center;
  padding: 15px 10px;
  background-color: #f5f7fa;
  border-bottom: 1px solid #D8D9D9;
}

.directory-title {
  flex-grow: 1;
  /* text-align: center; */
  color:black;
  font-weight: 600;
}

.directory-footer {
  border-top: 1px solid #D8D9D9;
  padding: 15px;
  text-align: center;
  background-color: #f5f7fa;
}
.link {
  color: #409eff;
  cursor: pointer;
}
:deep(.el-input__inner) {
  height:40px;
  line-height:40px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .14) !important;
  border-radius: 20px;
}
.transition-box {
  height: 450px;
  overflow-y: scroll;
}
</style>
